@import "../../_variables";

footer {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  background-color: $footer-background;
  color: $footer-font;
  align-items: center;
  margin-top: 50px;
}

.footer-links {
  background-color: $primary-color;
  margin: 0 auto;
  width: 100%;

  div {
    width: $container-xl;
    margin: 0 auto;

    ul {
      list-style-type: none;
      width: 50%;
      padding-left: 10px;
      li {
        margin: 15px 0;
      }
    }
  }
}

.footer-down {
  background-color: $footer-background;
  width: 100%;

  div {
    width: $container-xl;
    display: flex;
    flex-direction: row;
    margin: 0 auto;
    align-self: center;
    p {
      font-size: 0.85em;
    }
    div {
      display: block;
      background-color: $footer-background;
      width: 50%;

      i {
        margin-left: 10px;
      }
    }
  }
}
