@import "../../_variables";

.header {
  display: flex;
  background-color: $background-color;
  width: 80%;
  margin: 30px 0;
}

.logo {
  width: 50%;
  text-align: left;
  margin: auto 0;
  img {
    width: 200px;
  }
}

.right-menu {
  width: 50%;
  display: flex;
}

.cart-small {
  display: flex;
  flex-direction: row;
  margin-left: auto;
  a {
    align-self: center;
    margin-left: 5px;
    text-decoration: none;
    color: $heronav-font-color;

    img {
      height: 30px;
      width: auto;
      align-self: center;
    }
  }
}
